.people {
  --columns-count: 2;
  grid-template-columns: repeat(var(--columns-count), 200px);
  justify-content: center;
  column-gap: 35px;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    --columns-count: 4;
  }
}

body {
  box-sizing: border-box;
  margin: 0;
}

.person {
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
  font-family: Arial, sans-serif;
  font-size: 20px;
  line-height: 23px;
  transition: transform .3s;
  display: flex;
  position: relative;
}

.person__photo {
  box-sizing: border-box;
  width: 200px;
  height: 231px;
  margin-bottom: 15px;
}

.person__photo img {
  object-fit: fill;
  border-radius: 50%;
  width: 100%;
  height: 100%;
}

.person__photo-rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  font-size: 16px;
  position: absolute;
  top: 40%;
  left: 85%;
  transform: translate(-50%, -50%);
}

.person__details {
  color: #2f4f4f;
  justify-content: center;
  align-items: center;
}

.person__extra {
  visibility: hidden;
  color: #000;
  font-size: 18px;
}

.person:hover {
  background-color: #eee;
  border-radius: 10px;
}

.person:hover__extra {
  visibility: visible;
}
